"use client"

import { useState } from 'react';
import Image from 'next/image';
import { Notice } from '@/generated/prisma'; // Prisma가 생성한 Notice 타입을 가져옵니다.

interface NoticeBoardProps {
  notices: Notice[];
}

export default function NoticeBoard({ notices }: NoticeBoardProps) {
  const [activeTab, setActiveTab] = useState(0);

  return (
    <>
      {/* 탭 & 검색 */}
      <div className="flex items-center justify-between mb-8">
        <div className="flex gap-8 text-lg font-semibold">
          {['공지사항', '뉴스'].map((t, i) => (
            <button key={t} onClick={() => setActiveTab(i)} className={`${activeTab === i ? 'text-[#04AAAB]' : 'text-gray-500'} focus:outline-none`}>
              {t}
            </button>
          ))}
        </div>
        <div className="relative w-64 hidden md:block">
          <input type="text" placeholder="검색" className="w-full border rounded-md py-2 pl-3 pr-10 text-sm focus:outline-none focus:ring-2 focus:ring-[#04AAAB]" />
          <Image src="/icon_search.png" alt="검색" width={16} height={16} className="absolute right-3 top-1/2 -translate-y-1/2" />
        </div>
      </div>

      {/* 리스트 */}
      <div className="border-t-8 border-gray-100 pt-10">
        {activeTab === 0 && notices.map((notice, idx) => (
          <div key={notice.id} className="grid grid-cols-12 text-sm py-4 border-b border-gray-100 hover:bg-gray-50">
            <div className="col-span-1 text-center text-gray-400">{notices.length - idx}</div>
            <div className="col-span-9 truncate px-2 text-gray-700 hover:text-[#04AAAB] cursor-pointer">{notice.title}</div>
            <div className="col-span-2 text-right text-gray-400 pr-4">{new Date(notice.createdAt).toLocaleDateString()}</div>
          </div>
        ))}
        {activeTab === 0 && notices.length === 0 && (
          <div className="text-center py-10 text-gray-500">
            게시된 공지사항이 없습니다.
          </div>
        )}
        {activeTab === 1 && (
           <div className="text-center py-10 text-gray-500">
            뉴스는 준비 중입니다.
          </div>
        )}
      </div>
    </>
  );
} 